import React from 'react'

function ToDo({ state, removeTask }) {
    return (
        <div key={state.id}  >
            <div className="todo-container">
                {state.task}
            </div>
            <div className="item-delete" onClick={() => removeTask(state.id)}>
                X
            </div>
        </div>
    )
}

export default ToDo